.postedProperty {
	width: 60%;
	margin: 16px auto;
	border: 1px solid #eee;
	box-shadow: 0 2px 3px #ccc;
	padding: 16px;
	text-align: center;
}

.borderBottom {
  border-bottom: 2px solid grey;
}

@media (min-width: 500px) {
	.postedProperty {
			width: 450px;
	}
}
.text {
	margin: 20px;
	cursor: pointer;
	font-size: larger;
	background-color: green;
	padding: 5px;
}

nav {
	background-color: gray;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
}
.App {
	/* background-color: #151924; */
	color: black;
} 

.NewProperties {
	width: 80%;
	margin: 20px auto;
	border: 1px solid #eee;
	box-shadow: 0 2px 3px #ccc;
	text-align: center;
}

.NewProperties label {
	display: block;
	margin: 10px auto;
	text-align: center;
	font-weight: bold;
}

.NewProperties input,
.NewProperties textarea,
.NewProperties select {
	display: block;
	width: 80%;
	box-sizing: border-box;
	border: 1px solid black;
	outline: none;
	font: inherit;
	margin: auto;
}

.NewProperties button {
	margin: 15px 0;
	padding: 10px;
	font: inherit;
	border: 1px solid white;
	background-color: transparent;
	cursor: pointer;
}

.NewProperties button:hover,
.NewProperties button:active {
	color: white;
	background-color: #fa923f;
}
body {
	background-color: rgb(49, 66, 88);
	color: black;
}
